@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap');

.app {
  text-align: center;
  padding: 4vh 5% 2%;
}

.app * {
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
}

@media screen and (max-width: 811px) {
  .alerts-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    position: relative;
    width: 250px;
  }
}

@media screen and (min-width: 812px) {
  .alerts-container {
    position: fixed;
    top: 0px;
    left: 20px;
    width: 250px;
    z-index: 1000;
  }
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  margin-bottom: 20px;
}

.city {
  width: 250px;
}

#city-search {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  position: absolute;
  top: 45px;
  z-index: 2;
  align-self: center;
  margin: 0;
  font-size: 14px;
  box-shadow: 0px 5px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions>li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions>li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions>li:hover {
  background-color: #bcdcdb;
}

.charts-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 10px 0;
}

@media screen and (max-width: 992px) {
  .charts-container {
    grid-template-columns: 1fr;
  }
}

.event {
  padding: 10px 10px 40px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.title {
  font-weight: 700;
  font-size: 110%;
}

.details-btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  background-color: AliceBlue;
}

.details-btn:hover {
  background-color: #ccc;
}

.custom-tooltip {
  border: solid 2px #ccc;
  padding: 0 10px;
  background-color: white;
}

.loadingspinner {
  margin: auto;
  width: 50%;
  pointer-events: none;
  width: 2.5em;
  height: 2.5em;
  border: 0.4em solid transparent;
  border-color: #ccc;
  border-top-color: #3d82c0;
  border-radius: 50%;
  animation: loadingspin 1s linear infinite;
}

@keyframes loadingspin {
  100% {
    transform: rotate(360deg)
  }
}

.hidden {
  display: none;
}